import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import CircularProgress from "@mui/material/CircularProgress";
import VisibilityIcon from "@mui/icons-material/Visibility";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import PasswordIcon from "@mui/icons-material/Password";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import MarkEmailReadIcon from "@mui/icons-material/MarkEmailRead";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import PersonIcon from "@mui/icons-material/Person";
import AudiotrackIcon from "@mui/icons-material/Audiotrack";
import PianoIcon from "@mui/icons-material/Piano";
import axios from "axios";
import {
  Alert,
  FormControl,
  FormControlLabel,
  InputAdornment,
  MenuItem,
  Radio,
  RadioGroup,
  Snackbar,
  Stack,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import Visibility from "@mui/icons-material/Visibility";

const theme = createTheme();
export default function SignUpSide() {
  const [error, setError] = React.useState("");
  const [succees, setsuccees] = React.useState("");
  const [showPassword, setShowPassword] = React.useState(false);

  const history = useNavigate();
  const [open, setOpen] = React.useState(false);
  const [resToSignIn, setResToSignIn] = React.useState(false);

  const [loading, setLoading] = React.useState(false);

  const [countries, setCountries] = React.useState([]);
  const [selectedCountry, setSelectedCountry] = React.useState("");
  const [zones, setZones] = React.useState([]);
  const [selectedZone, setSelectedZone] = React.useState("");
  const [neighborhoods, setNeighborhoods] = React.useState([]);
  const [selectedNeighborhood, setSelectedNeighborhood] = React.useState("");

  React.useEffect(() => {
    axios
      .get("https://restcountries.com/v3.1/all")
      .then((response) => {
        setCountries(response.data);
      })
      .catch((error) => console.log(error));
  }, []);

  React.useEffect(() => {
    if (selectedCountry) {
      axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${selectedCountry}&appid=a5048198ca0355e38d2108b0cace217b`
        )
        .then((response) => {
          setZones(response.data.weather);
        })
        .catch((error) => console.log(error));
    }
  }, [selectedCountry]);

  React.useEffect(() => {
    if (selectedZone) {
      axios
        .get(
          `https://api.openweathermap.org/data/2.5/forecast?q=${selectedZone}&appid=a5048198ca0355e38d2108b0cace217b`
        )
        .then((response) => {
          setNeighborhoods(response.data.list);
        })
        .catch((error) => console.log(error));
    }
  }, [selectedZone]);

  const handleCountryChange = (event) => {
    setSelectedCountry(event.target.value);
    setSelectedZone("");
    setSelectedNeighborhood("");
  };

  const handleZoneChange = (event) => {
    setSelectedZone(event.target.value);
    setSelectedNeighborhood("");
  };

  const handleNeighborhoodChange = (event) => {
    setSelectedNeighborhood(event.target.value);
  };

  const currenciesGroupe = [
    {
      value: "A+",
      label: "A+",
    },
    {
      value: "A-",
      label: "A-",
    },
    {
      value: "B+",
      label: "B+",
    },
    {
      value: "B-",
      label: "B-",
    },
    {
      value: "AB+",
      label: "AB+",
    },
    {
      value: "AB-",
      label: "AB-",
    },
    {
      value: "O+",
      label: "O+",
    },
    {
      value: "O-",
      label: "O-",
    },
    {
      value: "",
      label: "Je ne connais pas",
    },
  ];

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true);
    if (!navigator.onLine) {
      setLoading(true);
      setOpen(true);
      return setError(
        "Si la connexion n'est pas active, vérifiez la connexion"
      );
    }

    // else if (!acceptTermsValue) {
    //   setLoading(false)
    //     setOpen(true)
    //   setError("Remplissez touts les champs")
    //   }
    else {
      const data = new FormData(event.currentTarget);
      axios
        .post(`${process.env.REACT_APP_URL_USER}/register`, {
          nom: data.get("firstName"),
          prenom: data.get("lastName"),
          email: data.get("email"),
          tel: data.get("tel"),
          password: data.get("password"),
          quartier: data.get("quartier"),
          activite: data.get("activite"),
        })
        .then((res) => {
          setLoading(true);
          console.log(res.data.message);
          setResToSignIn(true);
          setOpen(true);
          setsuccees(res.data.message);

          setTimeout(() => {
            history("/login");
          }, 5000);
        })
        .catch((error) => {
          setLoading(false);
          setOpen(true);
          if (error.response.data.message)
            return setError(error.response.data.message);
          else setError("Veuillez vérifez votre connexion");
        });
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: "100vh" }}>
        <CssBaseline />

        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "blue" }}>
              {/* <LockOutlinedIcon /> */}
            </Avatar>
            <Typography component="h1" variant="h6" textTransform="uppercase">
              Inscription
            </Typography>
            <Box
              component="form"
              autoComplete=""
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
              <TextField
                margin="normal"
                required
                fullWidth
                id="firstName"
                label="Nom de famille"
                name="firstName"
                autoComplete="firstName"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonOutlineIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="lastName"
                label="Prenom"
                name="lastName"
                autoComplete="lastName"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email"
                name="email"
                autoComplete="email"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <MarkEmailReadIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="tel"
                label="Numéro de téléphone"
                name="tel"
                autoComplete="tel"
                // inputProps={{ maxLength: 8,minLength:8 }}
                autoFocus
                helperText="Mettez un numéro de téléphone sans l'indicative du pays: Ex:53000000"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <LocalPhoneIcon />
                    </InputAdornment>
                  ),
                }}
              />
              {/* <TextField
                fullWidth
                margin="normal"
                required
                id="pays"
                select
                value={selectedCountry}
                onChange={handleCountryChange}
                label="Pays"
                defaultValue=""
                name="pays"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AudiotrackIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {countries.map((country) => (
                  <MenuItem
                    key={country.name.common}
                    value={country.name.common}
                  >
                    {country.name.common}
                  </MenuItem>
                ))}
              </TextField>
              <TextField
                fullWidth
                margin="normal"
                id="ville"
                select
                value={selectedZone}
                onChange={handleZoneChange}
                disabled={!selectedCountry}
                label="Ville"
                defaultValue=""
                name="ville"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PianoIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {zones.map((zone) => (
                  <MenuItem key={zone.id} value={zone.name}>
                    {zone.name}
                  </MenuItem>
                ))}
              </TextField> */}

              {/* <TextField
                fullWidth
                margin="normal"
                required
                id="quartier"
                select
                value={selectedNeighborhood}
                onChange={handleNeighborhoodChange}
                disabled={!selectedZone}
                label="Votre quartier"
                defaultValue=""
                name="quartier"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PianoIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {neighborhoods.map((neighborhood) => (
                  <MenuItem key={neighborhood.dt} value={neighborhood.dt}>
                    {neighborhood.dt_txt}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                fullWidth
                margin="normal"
                required
                id="activite"
                select
                label="Votre activite"
                defaultValue=""
                name="activite"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PianoIcon />
                    </InputAdornment>
                  ),
                }}
              >
                {currenciesGroupe.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField> */}
              <TextField
                margin="normal"
                required
                fullWidth
                id="quartier"
                label="Votre quartier"
                name="quartier"
                autoComplete="quartier"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      {/* <MarkEmailReadIcon /> */}
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                id="activite"
                label="Votre activité"
                name="activite"
                autoComplete="activite"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      {/* <MarkEmailReadIcon /> */}
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="Mot de passe"
                // type="password"
                id="password"
                autoComplete="current-password"
                endIcon={<VisibilityIcon />}
                type={showPassword ? "text" : "password"}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PasswordIcon />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment
                      onClick={() => {
                        setShowPassword(!showPassword);
                      }}
                      sx={{ cursor: "pointer" }}
                      position="end"
                    >
                      {" "}
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </InputAdornment>
                  ),
                }}
              />

              {/* <Box>
                <Box>
                  <FormControlLabel label="j'accepte les conditions d'utulisation" control={<Checkbox checked={acceptTermsValue} color="secondary" onChange={(e) => setAacceptTermsValue(e.target.checked)} />} >
                  </FormControlLabel>
                 
                </Box>
             </Box> */}
              {loading ? (
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <CircularProgress />
                </Box>
              ) : (
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{
                    mt: 3,
                    mb: 2,
                    background: {
                      md: "#0E1D34",
                      xs: "#0E1D34",
                      "&:hover": {
                        background: "#0E1D34",
                      },
                    },
                  }}
                >
                  Je m'inscire
                </Button>
              )}

              <Grid container>
                <Grid item>
                  <Button
                    sx={{ textTransform: "capitalize" }}
                    onClick={() => {
                      history("/login");
                    }}
                  >
                    {"j'ai déja un compte ? Je me connecte"}
                  </Button>
                  {/* <Button
                    sx={{ textTransform: "capitalize" }}
                    onClick={() => {
                      history("/politique");
                    }}
                  >
                    Politique de confidentialité
                  </Button> */}
                  <Button
                    sx={{ textTransform: "capitalize" }}
                    onClick={() => {
                      history("/condtion_generale");
                    }}
                  >
                    Condition d'utilisation
                  </Button>
                </Grid>
              </Grid>
            </Box>
          </Box>
        </Grid>
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: `url('logo.jpg')`,
            backgroundRepeat: "no-repeat",
            objectFit: "cover",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />
      </Grid>
      {resToSignIn ? (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              {succees}
            </Alert>
          </Snackbar>
        </Stack>
      ) : (
        <Stack spacing={2} sx={{ width: "100%" }}>
          <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="error"
              sx={{ width: "100%" }}
            >
              {error}
            </Alert>
          </Snackbar>
        </Stack>
      )}
    </ThemeProvider>
  );
}
