import { useNavigate } from "react-router-dom";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import "../../styles/userprofil.scss";
import { setgetusers } from "../../../features/users.reducers";
import { setgetUsers } from "../../../features/user.reducers";
import "../home/card.scss";
import { getAllPostAlls } from "../../../features/allposts.reducers";
import Navbar from "../../navigation/navbar";
import "./userprofil.scss";
import Notfound from "../notfound";
import Listpost from "../home/listpost";

const UserProfil = () => {
  const chats = useSelector((state) => state.chats.chats);

  const allPosts = useSelector((state) => state.allposts.allposts);
  const history = useNavigate();

  const user = useSelector((state) => state.user.user);

  const dispatch = useDispatch();

  const [data, setData] = useState({});

  const href = window.location.href;
  const id = href.slice(34);

  const RequestUserChats = async () => {
    const res = await axios
      .post(`http://localhost:7500/api/chat`, {
        senderId: user._id,
        receiverId: data._id,
      })
      .catch((error) => {
        console.log(error);
      });
    // .catch(err => console.log(err));
    const datas = await res.data;
    return datas;
  };

  const Chats = () => {
    if (chats.members?.find((id) => id === data._id)) {
      return history("/message");
    } else
      return RequestUserChats().then((data) => {
        // dispatch(setgetUserChats(data));
      });
  };
  const sendRquest = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/${id}`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };

  const sendRquestUsers = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}`, {
      withCredentials: true,
    });
    // .catch((err) => console.log(err));
    const data = await res.data;
    return data;
  };
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_POST}`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };
  const sendRquestUser = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };
  useEffect(() => {
    sendRquestUser().then((data) => {
      dispatch(setgetUsers(data.user));

      // console.log(data);
    });
  }, [dispatch]);

  useEffect(() => {
    sendRquestUsers().then((data) => {
      dispatch(setgetusers(data));
    });
  });
  useEffect(() => {
    sendRquestPost().then((data) => {
      dispatch(getAllPostAlls(data));
    });
  });
  useEffect(() => {
    sendRquest().then((data) => {
      setData(data);
      //  console.log(data)
    });
  });

  console.log(chats.members);

  return user ? (
    <>
      <Navbar />
      <>
        {data ? (
          <div className="userProfil">
            <div className="row py-5 px-4">
              <div className="card-container">
                <img
                  className="round"
                  src={data.picture}
                  width="40px"
                  alt="user"
                />
                <h3>{data.nom}</h3>
                <h6>{data.prenom}</h6>
                <p>
                  {data.activite}
                  <br /> à {data.quartier}
                </p>
                <div className="buttons">
                  {data.quartier === user.quartier && data._id !== user._id ? (
                    <button onClick={Chats} className="primary">
                      {" "}
                      Message
                    </button>
                  ) : (
                    ""
                  )}
                </div>
              </div>
            </div>
          </div>
        ) : (
          <p>Chagement</p>
        )}
      </>

      {allPosts
        ?.filter((el) => {
          if (el.posterId === id) {
            return true;
          }
        })
        // .slice(0, count)
        .map((el) => {
          return <Listpost el={el} key={el._id} />;
        })}
    </>
  ) : (
    <Notfound />
  );
};

export default UserProfil;
