import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { setgetUserChats } from "../../features/chats.reducers";
import Navbar from "../navigation/navbar";
import "./chats.scss";
import Conversations from "./Conversations";

const Chats = () => {
  const dispatch = useDispatch();

  const user = useSelector((state) => state.user.user);
  const chats = useSelector((state) => state.chats.chats);
  const [onlineUsers, setOnlineUsers] = useState([]);

  const RequestUserChats = async () => {
    const res = await axios
      .get(`http://localhost:7500/api/chat/${user._id}`, {
        withCredentials: true,
      })
      .catch((err) => console.log(err));
    const data = await res.data;
    return data;
  };

  useEffect(() => {
    RequestUserChats().then((data) => {
      dispatch(setgetUserChats(data));
    });
  });

  const onlineStatut = (chat) => {
    const chatMembers = chat.members?.find((member) => member !== user._id);
    const online = onlineUsers.find((user) => user.userId === chatMembers);
    return online ? true : false;
  };
  return (
    <>
      {user && (
        <>
          <Navbar />
          <div className="chatsMessage">
            <div className="container clearfix">
              <div className="people-list" id="people-list">
                <div className="search">
                  <input type="text" placeholder="search" />
                  <i className="fa fa-search"></i>
                </div>
              </div>
            </div>
          </div>
          {chats
            ?.map((chat) => {
              return (
                <Conversations
                  online={onlineStatut(chat)}
                  chat={chat}
                  currentUser={user._id}
                />
              );
            })
            .sort()}
        </>
      )}
    </>
  );
};

export default Chats;
