import { Button } from "@mui/material";

import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { addPost, getAllPost } from "../../../features/post.reducers";
import { isempty, timestamParser } from "../../utils";
import "./postform.scss";

const Postform = () => {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user.user);
  const [isLoading, setisLoading] = useState(true);
  const [message, setmessage] = useState("");
  const [postPicture, setpostPicture] = useState(null);
  const [video, setvideo] = useState();
  const [file, setfile] = useState();

  // console.log(posterId,activitePost,villePost);
  const sendRquest = async () => {
    const res = await axios
      .get(`${process.env.REACT_APP_URL_POST}`, {
        withCredentials: true,
      })
      .catch((err) => console.log(err));
    const data = await res.data;
    return data;
  };

  const handlePostUpload = async () => {
    if (message || postPicture || video) {
      const data = new FormData();
      data.append("posterId", user._id);
      data.append("message", message);

      data.append("activitePost", user.activite);
      data.append("quartierPost", user.quartier);
      data.append("emailPoster", user.email);
      if (file) data.append("posts", file);
      data.append("video", video);
      const res = await axios.post(`${process.env.REACT_APP_URL_POST}`, data);

      const datas = await res.data;
      return datas;
    } else {
      alert("Veuillez entrez un méssage");
    }
  };
  const handlePost = async () => {
    handlePostUpload()
      .then((data) => {
        dispatch(addPost(data));
      })
      .then(() => {
        dispatch(getAllPost());
      });
    cancelPost();
  };
  const handlePicture = (e) => {
    setpostPicture(URL.createObjectURL(e.target.files[0]));
    setfile(e.target.files[0]);
    setvideo("");
  };
  const cancelPost = () => {
    setmessage("");
    setpostPicture("");
    setvideo("");
    setfile("");
  };
  const handleVideo = () => {
    let findLink = message.split(" ");
    for (let i = 0; i < findLink.length; i++) {
      if (
        findLink[i].includes("https://www.you") ||
        findLink[i].includes("https://you")
      ) {
        let embed = findLink[i].replace("watch?v=", "embed/");
        setvideo(embed.split("&")[0]);
        findLink.splice(i, 1);
        setmessage(findLink.join(" "));
        setpostPicture("");
      }
    }
  };
  useEffect(() => {
    if (!isempty(user)) {
      setisLoading(false);
    }
    handleVideo();  
  }, [user, message, video]);

  useEffect(() => {
    sendRquest().then((data) => {
      dispatch(getAllPost(data));
    });
  });

  return (
    <>
      <br />
      <br />
      <div className="post-container">
        {isLoading ? (
          <i className="fas fa-spinner fa-pulse"></i>
        ) : (
          <>
            <NavLink exact to="/profil">
              <div className="user-info">
                <img src={user.picture} alt="user-img" />
              </div>
            </NavLink>
            <div className="post-form">
              <textarea
                name="message"
                id="message"
                placeholder="Quoi de neuf ?"
                onChange={(e) => setmessage(e.target.value)}
                value={message}
              />
              {message || postPicture || video?.length > 20 ? (
                <li className="card-container">
                  <div className="card-left">
                    <img src={user.picture} alt="user-pic" />
                  </div>
                  <div className="card-right">
                    <div className="card-header">
                      <div className="pseudo">
                        <h3>{user.pseudo}</h3>
                      </div>
                      <span>{timestamParser(Date.now())}</span>
                    </div>
                    <div className="content">
                      <p>{message}</p>
                      <img src={postPicture} alt="" />
                      {video && (
                        <iframe
                          src={video}
                          frameBorder="0"
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                          allowFullScreen
                          title={video}
                        ></iframe>
                      )}
                    </div>
                  </div>
                </li>
              ) : null}
              <div className="footer-form">
                <div className="icon">
                  {isempty(video) && (
                    <>
                      <img src="./img/icons/picture.svg" alt="img" />
                      <input
                        type="file"
                        id="file-upload"
                        name="file"
                        accept=".jpg, .jpeg, .png"
                        onChange={(e) => handlePicture(e)}
                      />
                    </>
                  )}
                  {video && (
                    <button onClick={() => setvideo("")}>
                      Supprimer video
                    </button>
                  )}
                </div>

                <div className="btn-send">
                  {message || postPicture || video?.length > 20 ? (
                    <Button
                      className="cancel"
                      style={{ width: "80px", margin: 10 }}
                      onClick={cancelPost}
                      type="submit"
                      variant="outlined"
                      color="error"
                    >
                      Annuler
                    </Button>
                  ) : null}

                  <Button
                    className="send"
                    style={{ width: "80px", margin: 10 }}
                    onClick={handlePost}
                    type="submit"
                    variant="outlined"
                  >
                    Envoyer
                  </Button>
                </div>
              </div>
            </div>
          </>
        )}
      </div>
    </>
  );
};

export default Postform;
